import { useEffect, useRef, useState } from 'react';
import './App.css';
import LoadingOverlay from 'react-loading-overlay';
LoadingOverlay.propTypes = undefined

function load(src) {
    return new Promise((resolve, reject) => {
        const image = new Image();
        image.addEventListener('load', resolve);
        image.addEventListener('error', reject);
        image.src = src;
    });
}

function App() {
    const [duringDays, setDuringDays] = useState(0);
    const [loading, setLoading] = useState(true);
    const componentRef = useRef();

    useEffect(() => {
        const startingDate = new Date('6/12/2023');
        const nowDate = new Date();
        const difference = nowDate.getTime() - startingDate.getTime();
        setDuringDays(Math.ceil(difference / (1000 * 3600 * 24)));
    }, []);

    const image = 'cover.png';
    load(image).then(() => {
       componentRef.current.style.backgroundImage = `url(${image})`;
       setLoading(false);
    });

    return (
        <LoadingOverlay
            active={loading}
            spinner
            text='()'
            styles={{
                overlay: (base) => ({
                    ...base,
                    background: 'rgba(0, 0, 0, 1)',
                    height: '100vh'
                })
            }}
        >
            <div className="App">
                <div className='App-header' ref={componentRef}>
                    <h1>{duringDays}</h1>
                    <p>Now to Eternity</p>
                </div>
            </div>
        </LoadingOverlay>
    );
}

export default App;
